/** Fill some gaps in the lookup table with the same values for easy access. */
function expandLookupTable() {
    // Fill remaining values of lookup
    for (const [kelvin, xy] of Object.entries(lookupTable)) {
        for (let i = 1; i < 10; i++) {
            lookupTable[parseInt(kelvin) + i] = xy;
        }
    }
}

/**
 * Use the above mapping table to map from Kelvin to x/y.
 *
 * @param kelvin The kelvin value to convert to x/y.
 * @returns The x/y value for the given kelvin, or undefined if the kelvin is out of range (1000-20.010K).
 */
export function kelvinToXyLookup(kelvin: number): { x: number; y: number } | undefined {
    if (!lookupTableExpanded) {
        expandLookupTable();
        lookupTableExpanded = true;
    }
    return lookupTable[Math.round(kelvin)];
}
